
























import Vue from 'vue';
import ItemsList from './components/ItemsList.vue';
import ItemDetail from './components/ItemDetail.vue';
import VueI18n from 'vue-i18n';

import messagesEn from '@/assets/locales/en.json';
import messagesDe from '@/assets/locales/de.json';
import messagesIt from '@/assets/locales/it.json';

Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: 'de',
  messages: {
    en: messagesEn,
    de: messagesDe,
    it: messagesIt,
  },
});

export default Vue.extend({
  i18n,
  components: {
    ItemsList,
    ItemDetail,
  },
  props: {
    contentType: {
      type: String,
      default: 'Gastronomy',
    },
    contentIdList: {
      type: String,
      default: null,
    },
    category: {
      type: String,
      default: null,
    },
    language: {
      type: String,
      default: 'de',
    },
    pageSize: {
      type: Number,
      default: 20,
    },
  },
  data() {
    return {
      detailContentId: null,
      currentPage: 1,
    };
  },
  computed: {
    contentIds() {
      return this.contentIdList !== null ? this.contentIdList.split(',') : [];
    },
    islistAvailable() {
      return this.contentIds.length != 1;
    },
  },
  watch: {
    language: {
      immediate: true,
      handler(value) {
        this.$i18n.locale = value;
      },
    },
  },
  created() {
    if (!this.islistAvailable) {
      this.detailContentId = this.contentIds[0];
    }
  },
  methods: {
    showDetail(detailContentId) {
      this.detailContentId = detailContentId;
    },
    closeDetail() {
      this.detailContentId = null;
    },
    changeCurrentPage(pageNum) {
      this.currentPage = pageNum;
    },
  },
});
